@font-face {
    font-family: Montserrat;
    src: local('Montserrat Regular'), url('../../public/fonts/Montserrat-Regular.ttf') format(truetype);
}

@media (max-width : 450px) {
    .panel-button {
        padding: 2pt;
        background-color: rgba(200, 200, 200, .2);
        border-width: 0;
        border-radius: 4pt;
        margin: 1pt;
        width: 4em;
        height: 26pt;
        cursor: pointer;
    }

    .panel-button-span {
        display: none;
    }

    .panel-button-icon {
        width: 18pt;
    }
}

@media (min-width : 450px) {
    .panel-button {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: 2pt;
        border: none;
        background-color: rgba(200, 200, 200, .08);
        min-width: 10em;
        border-radius: 6pt;
        margin: 4pt;
        cursor: pointer;
    }
    
    .panel-button-icon {
        /* margin-right: 2em; */
        width: 16pt;
    }
    
    .panel-button-span {
        height: 100%;
        display: inline;
    }
}

html {
    height: 100%;
    margin: 0;
    padding: 0;
}
body {
    margin: 0;
    padding: 0;
    min-height: 100%;
}

.highlighted-as-value {
    background-color: rgb(228, 212, 190);
    text-decoration: dotted;
    padding: 4px;
    border-radius: 3px;
}

.bold-text {
    font-weight: bold;
    font-weight: normal;
    font-style: normal;
    padding: 3pt;
    animation-duration: 0.8s;
    border-radius: 4px;
    animation-name: bold-text-flashing;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.bold-text:hover {
    background-color: red;
    animation: none;
    color: white;
}

@keyframes bold-text-flashing {
    from {
      background-color: rgba(231, 158, 98, 0.9);
    }
  
    to {
        background-color: rgba(252, 213, 41, 0.9);
    }
  }

body {
    margin: 0;
    // overflow: hidden;
 
    width: 100%;
}

body:before {
    position: fixed;
    background-color: #dad4bb;
    box-shadow: inset 0 0 100px black;
    width:100%;
    height:100%;
    content:"";
    z-index: -2;
    pointer-events: none;
}

body::-webkit-scrollbar {
    display: none;
}

/* All delay classes will take half the time to start */
:root {
    --animate-delay: 0.1s;
  }

.dialog-text {
    border-radius: 8pt;
    margin: 2em 1em 1em 1em;
    padding: .4em;
    font-size: 14pt;
    background-color: aliceblue;
    // border: solid 1pt black;
    box-shadow: 0 0 12pt grey;
    overflow: hidden;
}

.variants {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
    margin: auto;
}

.personhub-tools {
    width: 460pt;
    border-radius: 12pt;
    // background-color:rgba(200, 200, 200, .08);
    // border: 1px solid grey;
    padding: 7pt;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
}

.bigvariant {
    border-radius: 8pt;
    border: none;
    display: block;
    min-height: 8em;
    width: 102pt;
    padding: 7pt;
    margin: 8pt;
    box-shadow: 0 2pt 12pt grey;
    color: #444b43;
    font-family: Montserrat;
}

.bigvariant img {
    max-width: 40pt;
    max-height: 40pt;
    margin: 6pt;
    opacity: .8;
}

.bigvariant.exit {
    outline: 2px solid grey;
}

.variant {
    border-radius: 8pt;
    border: none;
    display: block;
    width: 460pt;
    max-width: 80%;
    min-height: 1em;
    padding: 7pt;
    // border: 1px solid black;
    margin: 4pt 0;
    box-shadow: 0 2pt 12pt grey;
    color: #444b43;
    font-family: Montserrat;
}

.variant.locked {
    opacity: 0.5 !important;
    // outline: 1px solid red;
    cursor: not-allowed;
}

.variant.locked:hover {
    background-color: #838470;
    // color: red;
    cursor: not-allowed;
    text-decoration: line-through;
}

.variant:hover {
    background-color: #838470;
    color: black;
    cursor: pointer;
}

.dev-toolbar-footer {
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    opacity: .3;
}

.dev-toolbar-footer:hover {
    opacity: 1;
}

.dev-toolbar-footer button {
    display: block;
    border: 1px solid black;
    font-size: x-small;
    font-family: monospace;
}

.version-span {
    font-family: 'Courier New', Courier, monospace;
    font-size: smaller;
    margin-left: 2em;
}

// typing
.container-typing {
    display: inline-block;
  }
  

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
.typed-out {
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap;
    font-size: 1.6rem;
    width: 0;
    animation: typing 1s forwards;
}

.popup-panel-bg {
    // position: absolute;
    // width: 100%;
    // height: 100%;
    // // backdrop-filter: blur(1px);
}

.popup-panel {
    // position: fixed;
    min-height: 20%;
    max-height: 90%;
    overflow-y: auto;
    border-bottom: 2pt solid black;
    z-index: 1000;
    backdrop-filter: blur(2px);
    background:rgba(228, 212, 190, 0.917);
    box-shadow: 0 0 50em black;
    width: 100%;
}

.popup-panel-holder {
    height: 100%;
    width: 100%;
    z-index: 900;
    position: fixed;
}

.popup-panel-close-button {
    opacity: 0.2;
    background-color: #444b43;
    background: linear-gradient(180deg, rgba(0,0,0,0.6) 0%, rgba(0,0,0,0.0) 100%);
    border: none;
    // position: fixed;
    // bottom: 0;
    padding: 10%;
    height: 10%;
    cursor: pointer;
    width: 100%;
    display: block;
    z-index: 1000;
    transition: opacity .6s;
}

.popup-panel-close-button:hover {
    opacity: .9;
}

.panel-header {
    display: flex;
    border-bottom: 1pt solid rgba(0, 0, 0, 0.724);
}

.panel-header p {
    font-family: Montserrat, sans-serif;
    font-size: 21pt;
    margin-left: 2em;
}

.invisible {
    display: none;
}



.panel-button:hover {
    outline: 1px solid white;
    background-color: rgba(200, 200, 200, 0.4);;
}

.header-panel {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: baseline;
}

.panel-close-button {
    border: 0;
    width: 30pt;
    height: 30pt;
    border-radius: 50%;
    background: none;
    margin: 15pt;
    cursor: pointer;
    opacity: 0.5;
}

.panel-close-button:hover {
    // outline: 1px solid black;
    background-color:rgba(226, 152, 152, 0.4);
    opacity: 1;
}

.fact-icon {
    background:url('../../public/img/free-icon-chess-piece.png') 0 0 no-repeat;
    // display:block;
    background-position: center;
    width: 20px;
    min-height: 24px;
    content: "";
    padding: 24px 12px;
}

.fact-id {
    font-size: 10pt;
}

.fact-text-container {
    padding: 3px 1em;
}

.fact:hover {
    background-color:rgba(200, 200, 200, 0.4);

}

.fact {
    display: flex;
    flex-direction: row;
}


/* ACTIONS */

.variant.pop {
    color: brown;
    font-weight: 500;
}

.variant.push {
    color: darkblue;
}

.variant.conditional {
    color: darkmagenta;
}

/* AUTHOR // ACTOR */

.author-container {
    position: relative;
    top: -18pt;
    left: 2em;
    font-size: 20px;
    display: block;
    flex-direction: row;
    height: 30pt;
    min-width: 8em;
    float: left;
    margin-bottom: -30pt;
    
}

.inner-author-container {
    border-radius: 8px;
    display: flex;
    height: 28pt;
    justify-content: space-between;
    flex-direction: row;
    // outline: 1px solid black;
    background-color: rgba(250, 235, 215, 0.889);
    box-shadow: 0 0 10px grey;
}

.author-icon  img {
    height: 22pt;
    // border-right: 1px solid black;
    text-align: center;
    display: block;
    position: relative;
    top: 4px;
    margin: 0px 4px;
}

.author-name {
    font-size: 14pt;
    margin: 0px 4px;
    flex-grow: 2;
    margin: 5pt 11pt;
}

.debug-data-record {
    display: flex;
    flex-direction: row;
}

.debug-data-record > p {
    margin: 5pt;
    border-bottom: 1px dashed #444b436b;
    width: 90%;
    font-family: "Consolas", monospace;
    // font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.debug-key {
    min-width: 16em;
    text-align: right;
}

.debug-val {
    color: darkblue;
    font-size: large;
}

.history-panel-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.history-record-container {
    margin: 5pt;
    background-color: antiquewhite;
    padding: 0 6pt;
    border-radius: 4px 16px;
    max-width: 55em;
    min-width: 12em;
    border: 1px solid #444b435b;
    box-shadow: 0 4px 12px #444b4367;
}

.history-record-reply {
    font-size: large;
    // border-right: 2px solid #444b43;
    border-top: 2px solid #444b4367;
    text-align: right;
    position: relative;
    padding-right: 4pt;
}

.history-actor {
    margin: 0;
    padding-left: 1em;
    font-size: small;
    color: grey;
    border-left: 11pt solid grey;
}

.message-panel {
    position: fixed;
    right: 0px;
    padding: 8px;
}

.message {
    background: rgba(200, 183, 135, 0.772);
    border: 1px solid black;
    border-bottom: 2px solid black;
    // border-radius: 4px;
    z-index: 9999;
    height: 32pt;
    max-width: 35em;
    min-width: 10em;
    opacity: .8;
    display: flex;
    flex-direction: row;
    box-shadow: 0 0 40px black;

    animation-name: dissapearMessage;
    animation-duration: 7000ms;
    animation-fill-mode: forwards;
}

.message-panel:hover {
    opacity: .2;
}

.message p {
    font-size: 9pt;
    margin: 0;
    padding: 1pt;
    margin-right: 6pt;
}

.message-icon {
    width: 24px;
    height: 24px;
    margin: 8px;
}

.message .message-header {
    font-size: 10pt;
    font-family: Montserrat;
}

@keyframes dissapearMessage{
    0%{
      opacity: .1;
      transform: translateY(-60pt);
    }
    3%{
        opacity: 1;
        transform: translateY(0pt);
    }
    80%{
        opacity: .9;
    }
    100%{
      display: none;
      opacity: 0;
    }
  }